* {
    margin: 0;
    padding: 0;    
}
.container-fluid {
    margin: 0;
    padding: 0;
}

a {
    color: inherit !important;
    text-decoration: none;
    
    a:hover {
        color: inherit !important;
        text-decoration: underline;
    }
}

ul {
    margin-bottom: 0;
    li {
        list-style-type: none;
    }
}
input, button {
    outline: none;
    font-family: inherit;
}