@import 'reset';
@import 'global';

body {
    font-family: $font;
    background-color: $light-white;
}

/* Header Contents */
header {
    background-image: $grad-green-blue;
    color: $white;
    text-align: center;
    border-radius: 0 !important;
    
    h1 {
        color: $white;
    }
}
/*-- Header Contents --*/

nav.navbar {
    background-image: $grad-green-blue;
    font-size: 16px;
    color: $white;
    .navbar-toggler {
        background-image: $grad-red-orange;
    }
    .navbar-collapse {
        justify-content: space-between;
        .current, .current:hover, a:hover {
            background-image: $grad-red-orange;
            color: $white;
        }
    }
}

.d-flex {
    .btn {
        background-image: $grad-red-orange;
        color: $white;
    }
}

/* Social-Media Button */
.social-btn {
    display: flex;
    flex-direction: column;
    position: fixed;
    left: 0;
    z-index: 10;
    a {
        flex-grow: 1;
        text-align: center;
        padding: 10px;
        color: $white !important;
        background-image: $grad-red-orange;
        font-size: 20px;
    }
}
/*-- Social-Media Button --*/

/* Article Contents */
article {
    position: relative;
    background-color: $light-white;
}

/* Realtime Datetime dan Caption */
#date-time {
    p {
        padding: 5px 10px;
        background-image: $grad-red-orange;
        font-size: 13px;
        color: $white;
        font-weight: bolder;
        text-align: center;
    }
}
/*-- Realtime Datetime dan Caption --*/

/* Blockquote Style */
#attention, .card-body {
    line-height: 25px;
    blockquote {
        background-image: $grad-green-blue;
        color: $white;
        position: relative;
    }
    blockquote::before, blockquote::after {
        position: absolute;
        text-shadow: 0 0 2px $black;
        font-size: 50px;
    }
}

#attention {
    blockquote {
        border-radius: 40px;
        margin: 20px;
        padding: 20px;
        text-align: center;
    }
    blockquote::before {
        content: open-quote;
        top: 5px;
        left: 10px;
    }
    blockquote::after {
        content: close-quote;
        bottom: -20px;
        right: 10px;
    }
}
.card-body {
    blockquote {
        margin: 20px 0;
        padding: 10px;
        padding-left: 35px;
        font-weight: 500;
        text-shadow: 0 0 3px $black;
        font-style: oblique;
        font-size: 14px;
        border-radius: 10px;
    }
    blockquote::before {
        content: open-quote;
        top: 50%;
        left: 5px;
    }
}
/*-- Blockquote Style --*/

/* Breadcrumb */
.breadcrumb {
    font-size: 13px;
    line-height: 20px;
    background-color: $light-white;
    a {
        color: $white !important;
        background-image: $grad-green-blue;
        border-radius: 10px;
        padding: 2px 10px;
        margin: 0 5px;
    }
}
/*-- Breadcrumb --*/

#post {
    .card {
        border: none;
        .card-img-top {
            width: 50%;
        }
        /* Post Label/Tag */
        .badge {
            background-image: $grad-green-blue;
            border-radius: 15px;
            padding: 5px 10px;
            font-weight: normal;
        }
        /*-- Post Label/Tag --*/
    }
    img {
        width: 100%;
        border-radius: 5px;
    }
    h2, h3 {
        line-height: 25px;
        margin: 10px 0;
        color: $black;
        font-weight: bolder;
    }
    h3 {
        background-image: $grad-green-blue;
        color: $white;
        margin-top: 30px;
        padding: 10px;
        padding-left: 20px;
        border-left: 7px solid $black;
    }
}

/* Postingan */
.card-body {
    p, li {
        line-height: 30px;
        text-align: justify;
        font-size: 15px;
    }
    ol {
        list-style: none;
        counter-reset: list;
    }
    li {
        counter-increment: list;
    }
    li::before {
        content: counter(list);
        background-image: $grad-green-blue;
        padding: 0 5px;
        border-radius: 5px;
        color: $white;
        font-weight: bold;
        margin-right: 5px;
    }      
    p.featured-link {
        margin: 10px 0;
        padding-left: 10px;
        font-size: 14px;
        border-left: 5px solid $light-gray;
        a {
            color: $white !important;
            background-image: $grad-red-orange;
            padding: 2px 10px;
            border-radius: 5px;
        }
    }
}

/* Nama Penulis dan Tanggal Posting */
.post-attribute {
    margin-bottom: 10px;
    font-size: 13px;
    align-items: center;
}
.author-post {
    margin-right: 10px;
    .author-post a {
        color: $black;
    }
}
.date-post {
    margin-right: 10px;
}
#post {
    .post-attribute {
        img {
            width: 30px;
            border-radius: 50%;
            margin-right: 10px;
        }
    }
}
/*-- Nama Penulis dan Tanggal Posting --*/

/* Share on Social Media */
.share {
    a {
        line-height: 10px;
        margin: 0 3px;
        padding: 10px;
        width: 35px;
        border-radius: 50%;
        display: inline-block;
        background-image: $grad-green-blue;
        color: $white !important;
    }
}
/*-- Share on Social Media --*/

/* Post Tags */
.tag {
    margin: 20px;
    padding: 20px;
    background-color: $white;
    border-radius: 5px;
    line-height: 30px;
    h2 {
        background-image: $grad-green-blue;
        border-radius: 5px;
        text-align: center;
        color: $white;
        padding: 10px;
        margin-bottom: 20px;
    }
    li {
        counter-increment: list;
    }
    li::before {
        content: counter(list);
        background-image: $grad-green-blue;
        padding: 0 5px;
        border-radius: 5px;
        color: $white;
        font-weight: bold;
        margin-right: 5px;
    }      
}
/*-- Post Tags --*/

/* Books Style */
#book {
    .book-desc {
        background-color: $white;
        a {
            background-image: $grad-green-blue;
            color: $white !important;
            font-weight: bold;
            font-size: 13px;
        }
    }
}
/*-- Books Style --*/

/* Pages Style */
#page {
    display: flex;
    align-items: center;
    justify-content: space-between;
    .page-desc {
        width: 30%;
        > img {
            width: 70%;
            border-radius: 50%;
        }    
    }
    .page-about {
        width: 70%;
        text-align: left;
        line-height: 30px;
    }
    h3 {
        color: $light-gray;
    }
    p {
        margin-bottom: 10px;
    }
}
/*-- Pages Style --*/

/*-- Postingan --*/

/* Pagination */
.page-link {
    background-image: $grad-red-orange;
    margin: 0 2px;
    padding: 10px 15px;
    color: $white !important;
    font-size: 15px;
}
.page-link:hover {
    box-shadow: 0 0 5px $white;
    color: $white;
    background-image: $grad-green-blue;
}
.current-link {
    background-image: $grad-green-blue;
}
/*-- Pagination --*/
/*-- Article Contents --*/

/* Footer Contents */
footer {
    background-color: $white;
    text-align: center;
    li {
        display: inline-flex;
        a {
            color: $black;
        }
    }
    li, .credit {
        font-size: 15px;
        a {
            padding: 20px;
        }
    }
    .credit {
        padding: 20px;
        background-image: $grad-green-blue;
        color: $white;
        font-weight: bold;
    }
}
/*-- Footer Contents --*/

@media screen and (max-width: 990px) {
    /* Mobile Navigation */
    nav {
        background-image: $grad-green-blue;
        z-index: 10;
        position: sticky;
        font-size: 20px;
        top: 0;
    }
    /*-- Mobile Navigation --*/

    /* Social-Media Button */
    .social-btn {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        position: fixed;
        bottom: 0;
        width: 100%;
        z-index: 10;
        a {
            flex-grow: 1;
            text-align: center;
            padding: 10px;
            color: $white !important;
            background-image: $grad-red-orange;
            font-size: 20px;
        }
    }
    /*-- Social-Media Button --*/

    #post {
        .card {
            .card-img-top {
                width: 100%;
            }
        }
    }

    /* Article Contents */
    article {
        position: relative;
    }
    /*-- Article Contents --*/

    #page {
        flex-direction: column;
        .page-desc, .page-about {
            width: 100%;
        }
        .page-about {
            text-align: center;
            margin-top: 20px;
        }
    }
}

@media screen and (max-width: 576px) {
    nav.navbar {
        font-size: 16px;
        .navbar-brand, button, input {
            font-size: inherit;
        }
    }

    .card-body {
        text-align: center;
    }

    footer {
        li {
            a {
                padding: 20px 10px;
            }
        }
        li, .credit {
            font-size: 13px;
        }
    }
}