@font-face {
    font-family: 'Montserrat';
    src: url('../fonts/Montserrat-Regular.ttf');
}

$font: Montserrat;
$black: #013557;
$white: #fff;
$grad-green-blue: linear-gradient(-45deg, #00ffbf, #1b8efa);
$grad-red-orange: linear-gradient(45deg, #ff3c00, #fab31b);
$selection: #00ffbf;
$light-gray: #999;
$light-white: #f3f3f3;

::selection {
    background-color: $selection;
    color: $black;
}
body {
    font-family: $font;
    background-color: $light-white;
    color: $black;
}
hr {
    border: 1px solid #ededed;
    margin: 10px 0;
}

h1 {
    font-size: 35px !important;
}
h2 {
    font-size: 28px !important;
}
h3 {
    font-size: 20px !important;
}

/* Syntax Highlighting */
.language-plaintext {
    color: #ff3c00;
    font-size: 15px;
}
.highlight {
    padding: 7px 7px 1px 7px;
    font-size: 15px;
    line-height: 20px;
    border-radius: 5px;
    background-color: $light-white;
    max-height: 400px;
    text-align: left;
    .lineno {
        font-size: 15px;
        color: $black;
        padding: 0 5px;
    }
    .code {
        font-size: 17px;
    }
}
/*-- Syntax Highlighting --*/

/* Smooth Scrolling */
html {
  scroll-behavior: smooth;
}
/* -- Smooth Scrolling -- */

/* Scroll to Top */
#toTop {
    padding: 10px;
    background-image: $grad-green-blue;
    color: $white;
    bottom: 20px;
    right: 10px;
    z-index: 15;
    display: none;
    cursor: pointer;
    position: fixed;
    opacity: .5;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=95)";
    border-radius: 50%;
    width: 45px;
    height: 45px;
    text-align: center;
}
    
#toTop:hover {
    opacity: 1;
}
/* -- Scroll to Top -- */

/* Search Style */
.search {
    section {
        margin: 10px 0;
        padding: 20px 20px 10px 20px;
        background-color: $white;
        border-radius: 5px;
        h3 {
            background-image: $grad-green-blue;
            border-left: 5px solid $black;
            padding: 15px;
            margin-bottom: 20px;
            border-radius: 10px;
            color: $white;
            span {
                display: inline-block;
                margin-right: 10px;
                padding: 5px 10px;
                background-image: $grad-red-orange;
                border-radius: 5px;
            }
        }
        a {
            display: inline-block;
            background-image: $grad-green-blue;
            padding: 3px 10px;
            font-size: 15px;
            border-radius: 5px;
            color: $white !important;
        }
    }
}
/*-- Search Style --*/

@media screen and (max-width: 990px) {
    #toTop {
        bottom: 70px;
        right: 20px;
    }
}